import {
    Modifier,
} from './types';

import parseModifiers from './parseModifiers';


function purebem(block: string) {
    return function(elementOrModifierParam?: string | Modifier, ...modifiers: Modifier[]): string {
        if (typeof block !== 'string' || block === '') {
            return '';
        }

        const element = typeof elementOrModifierParam === 'string'
            ? elementOrModifierParam
            : void 0;

        const parsedModifiers = typeof elementOrModifierParam === 'string'
            ? parseModifiers(modifiers)
            : parseModifiers([elementOrModifierParam !== void 0 ? elementOrModifierParam : {}, ...modifiers]);

        const baseClassName = element !== void 0 ? `${block}__${element}` : `${block}`;
        const modifiersClassNames = parsedModifiers.map((modifier) => `${baseClassName}--${modifier}`);

        return [baseClassName, ...modifiersClassNames].join(' ');
    };
}

purebem.many = function(...args: string[]): string {
    return args.filter((arg) => typeof arg === 'string').join(' ');
};

export default purebem;
