import {
    Modifier,
    ObjectValue,
} from './types';


function convertCamelCase(key: string): string {
    return key.replace(/([a-z](?=[A-Z]))/g, '$1-').toLowerCase();
}

function fromKeyOrValue(key: string, value: ObjectValue): string {
    return typeof value === 'string'
        ? value
        : convertCamelCase(key);
}

function isObject(value: unknown): value is object {
    return typeof value === 'object' && value !== null && !Array.isArray(value);
}

function isValidObjectPropertyValue(value: ObjectValue): boolean {
    return (typeof value === 'string' && value !== '') || value === true;
}

function parseArrayItem(obj: Modifier): string[] {
    if (Array.isArray(obj)) {
        return obj.filter(Boolean);
    }

    if (isObject(obj)) {
        return Object.entries(obj)
            .filter(([, value]) => isValidObjectPropertyValue(value))
            .map(([key, value]) => fromKeyOrValue(key, value));
    }

    return [];
}

function byModifier(prev: string[], curr: Modifier): string[] {
    const modifiers = parseArrayItem(curr);
    return [...prev, ...modifiers];
}

export default function parseModifiers(array: Modifier[] = []): string[] {
    return array.reduce(byModifier, []);
}
